
body {
  margin: 0;
  background: linear-gradient(90deg, #161e41 1.18%, #243169 100%);
}

.timer--title{
  color: white;
  text-align: center;
  font-family: "Inter";
  font-weight: 700 ;
  font-size: 30px ;
}

.timer--main{
  font-family:'Cursed Timer ULiL';
  background: black;
  color: #F94F6D ;
  height: 160px;
  width: 320px;
  margin: auto;
  padding-top: 30px;
  margin-top: 20px;
  margin-bottom: 30px;
  border-radius: 5px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.699);
}

.timer--button{
  background: #1B244A;
  color:#9AABD8;
  border: solid 2px #9AABD8;
  width: 90px;
  height: 50px;
  border-radius: 5px;
  font-family: 'Cursed Timer ULiL';
  font-size: 20px;
  margin-top: 20px;
}

.count--container{
  display: flex;
  justify-content: space-around;

}

.title{
  color: white;
  text-align: center;
  padding-top: 40px;
  font-family: "Inter";
  font-weight: 700 ;
  font-size: 40px ;
}

.number1{
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  color: #F94F6D;
  font-family: 'Cursed Timer ULiL';
  font-weight: 400;
  font-size: 90px;
  width: 200px;
  height: 160px;
  border-radius: 5px;
  margin-top: 0;
  margin-bottom: 20px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.699)
}

.number2{
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  color: #F94F6D;
  font-family: 'Cursed Timer ULiL';
  font-weight: 400;
  font-size: 90px;
  width: 200px;
  height: 160px;
  border-radius: 5px;
  margin-top: 0;
  margin-bottom: 20px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.699);
}

.counter--button{
  background: #1B244A;
  color:#9AABD8;
  border: solid 2px #9AABD8;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  font-family: 'Cursed Timer ULiL';
  font-size: 20px;
  margin-left: 10px;
  margin-bottom: 30px ;
}

.clear--button{
  background: #1B244A;
  color:#9AABD8;
  border: solid 2px #9AABD8;
  width: 100px;
  height: 60px;
  border-radius: 5px;
  font-family: 'Cursed Timer ULiL';
  font-size: 20px;
  margin-left: 10px;
  margin-top: 200px;
}

.counter--button:focus , .clear--button:focus ,.timer--button:focus{
  outline: none;
}

.counter--button:active, .clear--button:active ,.timer--button:active{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}